@import "../vendor/bootstrap/css/bootstrap.min.css";
@import "../vendor/bootstrap-icons/bootstrap-icons.css";
@import "../vendor/select2/dist/css/select2.min.css";
@import "../vendor/select2/dist/css/select2-bootstrap4.min.css";
@import "../vendor/aos/css/aos.css";
@font-face {
    font-family: Nunito;
    src: url("../font/Nunito-Regular.ttf");
    src: url("../font/Nunito-Regular.ttf") format("ttf"),
        url("../font/Nunito-Regular.ttf") format("truetype");
}
